/*gnb*/
.gnb {
  width: 100%;
  position: absolute;
  left: 0;
  top: 50px;
  background-color: #fff;
  background-repeat: repeat-x;
  background-position: center top;
  box-shadow: 0px 7px 11px -13px rgba(0, 0, 0, 0.75);
  /*한개씩 떨어짐*/
}

.gnb .gnb-ul {
  display: table;
  width: 1200px;
  padding-left: 0px;
  margin: 0 auto;
}

.gnb .gnb-menu {
  display: table-cell;
  position: relative;
}

.gnb .gnb-menu > a {
  display: block;
  text-align: center;
  color: #000;
  height: 80px;
  line-height: 80px;
  text-decoration: none;
  font-size: 18px;
  font-family: "Noto Sans KR", "Nanum Gothic", sans-serif;
  font-weight: 500;
}

.gnb .gnb-menu.over > a,
.gnb .gnb-menu.focus > a,
.gnb .gnb-menu > a:hover,
.gnb .gnb-menu > a:focus {
  color: #f6804e;
}

.gnb .sub-nav {
  visibility: hidden;
  height: 0;
  position: absolute;
  top: 80px;
  left: 0;
  width: 100%;
  z-index: 98;
  background-color: #fff;
  padding-top: 0;
  opacity: 0;
  transition: all 0.3s ease 0s;
}

.gnb .gnb-menu.focus .sub-nav,
.gnb .gnb-menu.over .sub-nav,
.gnb.selected .gnb-menu.focus .sub-nav {
  visibility: visible;
  height: auto;
  opacity: 1;
}

.gnb .sub-nav__box {
  border: 2px solid #00235d;
  border-top: 0px;
  padding-left: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  transition: all 0.3s ease 0s;
}

.gnb .sub-nav__box__list > a {
  display: block;
  padding: 8px 0 8px 10px;
  color: #000;
  font-size: 14px;
  font-size: 1.4rem;
}

.gnb .sub-nav__box__list > a:hover,
.gnb .sub-nav__box__list > a:focus {
  text-decoration: underline;
  color: #f6804e;
}

.gnb .gnb-menu.over .sub-nav__box,
.gnb.selected .gnb-menu.focus .sub-nav__box {
  padding-bottom: 20px;
  padding-top: 20px;
}

* + html .gnb {
  position: absolute;
  top: 0;
  padding-top: 0;
}

