/* checkbox */
input[class="check-type"] + label {
  display: inline-block;
  height: 30px;
  line-height: 30px;
  cursor: pointer;
  position: relative;
  padding-left: 40px;
}

input[class="check-type"] + label:after {
  display: inline-block;
  content: ' ';
  width: 30px;
  height: 30px;
  box-sizing: border-box;
  position: absolute;
  top: 0px;
  left: 0px;
  border: 2px solid #cccccc;
  background-color: #dddddd;
  border-radius: 30px;
}

input[class="check-type"] + label:before {
  content: ' ';
  content: '';
  vertical-align: middle;
  display: inline-block;
  background-repeat: no-repeat;
  background-position: -186px -664px;
  background-image: url("../images/sprite.png");
  width: 12px;
  min-width: 12px;
  height: 9px;
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 9;
}

input[class="check-type"]:checked + label:after {
  background-color: #666666;
}

input[class="check-type"]:focus + label:after {
  border: 2px solid gold;
}

input[class="check-type"] {
  position: absolute;
  left: -9999px;
}

