/*	footer
==========*/
.footer {
  background-color: #fff;
  color: #666;
  position: relative;
  border-top: 1px solid #e5e5e5;
  font-family: "Noto Sans KR", "Nanum Gothic", sans-serif;
  /*background-image:url("../../commons/images/global/footer-bg.png");background-position:0 45px;background-repeat:repeat-x;*/
}

.footer a:hover,
.footer a:focus {
  text-decoration: underline;
}

.footer__in {
  width: 1200px;
  margin: 0 auto;
  position: relative;
  min-height: 150px;
}

.footer__in:after {
  content: ' ';
  clear: both;
  display: block;
  visibility: hidden;
}

* + html .footer__in {
  display: inline-block;
}

.foot_img {
  position: absolute;
  top: 35px;
  left: 0;
  content: '';
  vertical-align: middle;
  display: inline-block;
  background-repeat: no-repeat;
  background-position: 0px -95px;
  background-image: url("../images/sprite.png");
  width: 173px;
  min-width: 173px;
  height: 40px;
  text-indent: -9999px;
  font-size: 0;
}

.copy_box {
  position: absolute;
  top: 30px;
  left: 260px;
}

.copy_box ul:after {
  content: ' ';
  clear: both;
  display: block;
  visibility: hidden;
}

* + html .copy_box ul {
  display: inline-block;
}

.copy_box li {
  float: left;
  position: relative;
  padding-left: 30px;
}

.copy_box li a {
  font-size: 13px;
  font-size: 1.3rem;
  color: #000;
  vertical-align: top;
}

.copy_box li:before {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 15px;
  content: '';
  vertical-align: middle;
  display: inline-block;
  background-repeat: no-repeat;
  background-position: -800px -180px;
  background-image: url("../images/sprite.png");
  width: 1px;
  min-width: 1px;
  height: 13px;
}

.copy_box li:first-child:before {
  display: none;
}

.copy_box li:first-child {
  background-image: none;
  padding-left: 0;
}

.copy_box li:first-child a {
  background-image: none;
}

.copy_box li.persnal-rule a {
  color: #1d2f79;
}

.copy_box li.sns-li {
  background-image: none;
}

.copy_box2 {
  position: absolute;
  top: 50px;
  left: 260px;
  width: auto;
  padding-top: 20px;
  font-size: 12px;
}

.copy_box2 .item1 {
  margin-bottom: 5px;
  font-style: normal;
  font-size: 12px;
  font-size: 1.2rem;
  line-height: 1.5;
  color: #666;
}

.copy_box2 .item1 ul:after {
  content: ' ';
  clear: both;
  display: block;
  visibility: hidden;
}

* + html .copy_box2 .item1 ul {
  display: inline-block;
}

.copy_box2 .item1 ul li {
  float: left;
  padding-left: 20px;
}

.copy_box2 .item1 ul li:first-child {
  padding-left: 0;
  background-image: none;
}

.copy_box2 .item2 {
  font-style: normal;
  margin-top: 5px;
  margin-bottom: 4px;
  font-size: 12px;
  font-size: 1.2rem;
  line-height: 1.6;
  color: #666;
}

.copy_box3 {
  position: absolute;
  right: 0;
  top: 10px;
}

.copy_box4 {
  position: absolute;
  right: 0;
  top: 75px;
}

/*통계*/
.total-count {
  background-color: #399e90;
  padding: 10px 30px 10px 10px;
  border-radius: 30px;
}

.total-count:after {
  content: ' ';
  clear: both;
  display: block;
  visibility: hidden;
}

* + html .total-count {
  display: inline-block;
}

.total-count__dt {
  display: inline-block;
  color: #fff;
  margin-right: 5px;
  padding-left: 20px;
}

.total-count__dt:before {
  content: '';
  padding-right: 10px;
  padding-left: 14px;
  display: inline-block;
  content: '';
  vertical-align: middle;
  display: inline-block;
  background-repeat: no-repeat;
  background-position: -800px -418px;
  background-image: url("../images/sprite.png");
  width: 1px;
  min-width: 1px;
  height: 10px;
}

.total-count__dt:first-child:before {
  display: none;
}

.total-count__dd {
  display: inline-block;
  color: #fff;
}

/*푸더 셀렉트*/
.footer-select {
  border: 1px solid #717171;
}

.footer-select__item {
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
  position: relative;
  background-color: transparent;
  text-align: left;
  border: 0px solid #717171;
}

.footer-select__item:after {
  position: absolute;
  top: 50%;
  right: 0;
  margin-right: 10px;
  transform: translateY(-50%);
  content: ' ';
  content: '';
  vertical-align: middle;
  display: inline-block;
  background-repeat: no-repeat;
  background-position: -434px -664px;
  background-image: url("../images/sprite.png");
  width: 12px;
  min-width: 12px;
  height: 4px;
}

.footer-select__item select {
  width: 100%;
  background-color: transparent;
  appearance: none;
  min-width: 230px;
  vertical-align: top;
  color: #666;
  font-size: 14px;
  height: 38px;
  margin-top: 0;
  padding: 2px 2px 2px 10px;
  position: relative;
  border: 0px;
  font-family: "Noto Sans KR", "Nanum Gothic", sans-serif;
  /*
			@media screen and (min-width:0\0){
				& {width:125%}
			}
			*/
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  /*ie10~11 에서 셀렉트 기본디자인 무력화*/
}

.footer-select__item select::-ms-expand {
  display: none;
}

.lte-ie9 .footer-select__item select {
  width: 125%;
  vertical-align: top;
}

.footer-select__item select option {
  color: #000;
  font-family: "Nanum Gothic";
}

.footer-select__item select:focus {
  border: 1px solid gold;
  border-radius: 4px;
}

@media screen and (min-width: 1px) and (max-width: 768px) {
  .footer-select__item select {
    min-width: 200px;
  }
}

.sel_go_btn {
  display: inline-block;
  height: 38px;
  width: 43px;
  line-height: 38px;
  text-align: center;
  color: #fff;
  background-color: #272727;
  border-left: 1px solid #717171;
}

* + html .sel_go_btn {
  display: inline;
}

